import React, { Component } from "react";
import './styles.css';
import avatar from './MetaQuestAvatar.png';

export default class Home extends Component {
  constructor(props) {
    super(props);
  }


  render() {

    return (
      <div className="mainContent">
        <div>
          <div className="divAvatar">
            <img alt="My Oculus Avatar" src={avatar} />
          </div>
          <div className="divText">
            <h3>Hello!</h3>
            <p>I'm Matt the programmer. I'm a full-stack DotNet developer by day,
            but in the evening I like to take on some more interesting projects.
            Some of these projects include working with AI, writing block chain
            applications, creating 3D graphics and developing video games. This
            is a show case of some of my projects over the years.
            </p>
          </div>
        </div>
      </div>
    )
  }
}
